<template>
  <div class="tablet_settings">

    <div class="columns">
      <div class="column is-10 is-offset-1">
        <div class="box">
          <div>
            <h1>
              Tablet Settings
            </h1>
          </div>

          <!-- Show Current Settings -->
          <table v-if="settings" class="table is-hoverable is-fullwidth is-striped">
            <thead>
              <tr>
                <th>id</th>
                <th>project id</th>
                <th>Name</th>
                <th>Full Name</th>
                <th>Familiar Name</th>
                <th>Gender</th>
                <th>Country</th>
                <th>Language</th>
                <th>Timezone</th>
                <th>Actions</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td><span :title="settings.id" >id</span></td>
                <td><span :title="settings.projects_id" >id</span></td>
                <td>{{ settings.name }} </td>
                <td>{{ settings.full_name }}</td>
                <td>{{ settings.familiar_name }}</td>
                <td>{{ settings.gender }}</td>
                <td>{{ settings.country }}</td>
                <td>{{ settings.language }}</td>
                <td>{{ settings.timezone }}</td>
                <td>Update</td>
              </tr>
            </tbody>
          </table>

          <h2>Features</h2>

          <div class="columns">

            <div class="column">

              <b-field>
                <b-checkbox
                  name="documents"
                  id="documents"
                  size="is-medium"
                  v-model="settings.documents">
                    Display Documents
                </b-checkbox>
              </b-field>

              <b-field>
                <b-checkbox
                  name="calendar"
                  id="calendar"
                  size="is-medium"
                  v-model="settings.calendar">
                    Display Calendar
                </b-checkbox>
              </b-field>

              <b-field>
                <b-checkbox
                  name="videos"
                  id="videos"
                  size="is-medium"
                  v-model="settings.videos">
                    Display Videos
                </b-checkbox>
              </b-field>

              <b-field>
                <b-checkbox
                  name="websites"
                  id="websites"
                  size="is-medium"
                  v-model="settings.websites">
                    Display Websites
                </b-checkbox>
              </b-field>

              <b-field>
                <b-checkbox
                  name="exercises"
                  id="exercises"
                  size="is-medium"
                  v-model="settings.exercises">
                    Display Exercises
                </b-checkbox>
              </b-field>

              <b-field>
                <b-checkbox
                  name="photos"
                  id="photos"
                  size="is-medium"
                  v-model="settings.photos">
                    Display Photos
                </b-checkbox>
              </b-field>

              <b-field>
                <b-checkbox
                  name="photos_delete"
                  id="photos_delete"
                  v-model="settings.photos_delete">
                    Allow Photo Delete
                </b-checkbox>
              </b-field>

               <b-field>
                <b-checkbox
                  name="photos_slideshow"
                  id="photos_slideshow"
                  v-model="settings.photos_slideshow">
                    Show Photo Slideshow
                </b-checkbox>
              </b-field>

            </div> <!-- end column -->

            <div class="column">

              <b-field>
                <b-checkbox
                  name="request_call"
                  id="request_call"
                  size="is-medium"
                  v-model="settings.request_call">
                    Display Request Call
                </b-checkbox>
              </b-field>

              <b-field>
                <b-checkbox :value="true"
                  name="request_call"
                  id="request_call"
                  type="is-warning">
                    User 1
                </b-checkbox>
              </b-field>
              more user options here

              <b-field>
                <b-checkbox
                  name="video_call"
                  id="video_call"
                  size="is-medium"
                  v-model="settings.video_call">
                    Display Video Call
                </b-checkbox>
              </b-field>

              user options here

              <b-field>
                <b-checkbox
                  name="messages"
                  id="messages"
                  size="is-medium"
                  v-model="settings.messages">
                    Display Messages
                </b-checkbox>
              </b-field>

              <b-field>
                <b-checkbox
                  name="messages_reply"
                  id="messages_reply"
                  v-model="settings.messages_reply">
                    Show Messages Reply
                </b-checkbox>
              </b-field>

              user options here

            </div> <!-- end column -->

          </div> <!-- end columns -->

          <h2>Settings</h2>

          <div class="columns">

            <div class="column">

              <b-field>
                <b-checkbox
                  name="power"
                  id="power"
                  size="is-medium"
                  v-model="settings.power">
                    Display On/Off Button
                </b-checkbox>
              </b-field>

            </div> <!-- end column -->

            <div class="column">

              <b-field>
                <b-checkbox
                  name="kiosk"
                  id="kiosk"
                  size="is-medium"
                  v-model="settings.kiosk">
                    Lock Device
                </b-checkbox>
              </b-field>

            </div> <!-- end column -->

          </div> <!-- end columns -->

          <b-field label="Volume">
            <b-slider
              size="is-medium"
              tooltip-always
              v-model="settings.volume">
            </b-slider>
          </b-field>

          <button class="button is-success" @click="updateSettings">Save Changes</button>

        </div>
      </div> <!-- end Tablet Settings -->

    </div>
  </div>
</template>

<script>
export default {

  data () {
    return {
      settings: {}
    }
  },

  mounted () {
    this.getSettings()
  },

  methods: {

    // Get Tablet Settings
    getSettings () {
      this.$http.get('tablet_users/')
        .then(response => {
          this.settings = response.data
        })
    },

    updateSettings () {

    }

  } // end methods

}
</script>
